/**
 * Test Data Factories
 * 
 * Factory functions for creating test data with sensible defaults.
 * Follows the factory pattern to reduce test boilerplate and ensure consistency.
 */

import type { 
  Guest, 
  GuestGroup, 
  Event, 
  Activity, 
  Accommodation,
  RoomType,
  Location,
  Section,
  ContentPage,
  RSVP
} from '@/types';

/**
 * Create a test guest with optional overrides
 */
export function createTestGuest(overrides: Partial<Guest> = {}): Guest {
  const timestamp = Date.now();
  return {
    id: `test-guest-${timestamp}`,
    firstName: 'Test',
    lastName: 'User',
    email: `test.user.${timestamp}@example.com`,
    groupId: 'test-group-id',
    ageType: 'adult',
    guestType: 'wedding_guest',
    dietaryRestrictions: null,
    notes: null,
    plusOneAllowed: false,
    plusOneName: null,
    createdAt: new Date().toISOString(),
    updatedAt: new Date().toISOString(),
    ...overrides,
  };
}

